/*BODY*/
body {
  font-size: 20px;
  font-weight: 400;
  font-family: "Inter", sans-serif;
  line-height: 140%;
  color: #101010;
  margin: 0px;
}

/*CONTENER*/
.contener {
  max-width: 680px;
  margin: 0 auto;
}

/*LINK*/
.cs-link {
  color: #2563EB;
  text-decoration: none;
}
.cs-link:hover {
  text-decoration: underline;
}

.cs-link-dark {
  color: #101010;
}

.title-span {
  color: #7B7B7C;
}

.case-title {
  font-size: 32px;
  color: #000;
  line-height: 100%;
  font-weight: 500;
  margin: 0;
}

.header {
  padding: 28px 0px 28px 0px;
  border-bottom: 1px solid rgba(0, 0, 0, 0.08);
  position: sticky;
  top: 0;
  background-color: #fff;
}

.header__nav-wrapper {
  display: flex;
  gap: 8vw;
  justify-content: center;
}

.hero {
  background-color: #F5F5F7;
  padding: 80px 0px 80px 0px;
  /*TITLE*/
  /*INFO*/
}
.hero__title--text {
  margin: 0;
  font-size: 44px;
  font-weight: 500;
  line-height: normal;
  color: #000;
  text-align: center;
}
.hero__title--text-span {
  color: #7B7B7C;
}
.hero__info {
  margin-top: 80px;
  font-size: 16px;
  line-height: 140%;
  display: flex;
  gap: 18px;
  /*Item*/
}
.hero__info--right {
  display: flex;
  flex-direction: column;
  gap: 18px;
}
.hero__info--item {
  background: #fff;
  border-radius: 8px;
  padding: 20px 30px;
  width: 158px;
}
.hero__info--item-big {
  font-size: 24px;
  line-height: 140%;
  width: 364px;
  height: 216px;
  padding: 24px 30px;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}
.hero__info--item-big .hero__info--title {
  margin-bottom: auto;
}
.hero__info .hero__info--title {
  font-weight: 600;
  margin: 0px 0px 4px 0px;
}

.desktop {
  background-color: #F5F5F7;
  margin: 0;
  padding: 20px 0px 100px 0px;
  text-align: center;
}
.desktop__video {
  border-radius: 8px;
  overflow: hidden;
  margin-top: 40px;
  display: flex;
  flex-direction: column;
  gap: 0;
}

.assumptions {
  padding: 80px 0px;
}