body {
  color: #101010;
  margin: 0;
  font-family: Inter, sans-serif;
  font-size: 20px;
  font-weight: 400;
  line-height: 140%;
}

.contener {
  max-width: 680px;
  margin: 0 auto;
}

.cs-link {
  color: #2563eb;
  text-decoration: none;
}

.cs-link:hover {
  text-decoration: underline;
}

.cs-link-dark {
  color: #101010;
}

.title-span {
  color: #7b7b7c;
}

.case-title {
  color: #000;
  margin: 0;
  font-size: 32px;
  font-weight: 500;
  line-height: 100%;
}

.header {
  background-color: #fff;
  border-bottom: 1px solid #00000014;
  padding: 28px 0;
  position: sticky;
  top: 0;
}

.header__nav-wrapper {
  justify-content: center;
  gap: 8vw;
  display: flex;
}

.hero {
  background-color: #f5f5f7;
  padding: 80px 0;
}

.hero__title--text {
  color: #000;
  text-align: center;
  margin: 0;
  font-size: 44px;
  font-weight: 500;
  line-height: normal;
}

.hero__title--text-span {
  color: #7b7b7c;
}

.hero__info {
  gap: 18px;
  margin-top: 80px;
  font-size: 16px;
  line-height: 140%;
  display: flex;
}

.hero__info--right {
  flex-direction: column;
  gap: 18px;
  display: flex;
}

.hero__info--item {
  width: 158px;
  background: #fff;
  border-radius: 8px;
  padding: 20px 30px;
}

.hero__info--item-big {
  width: 364px;
  height: 216px;
  flex-direction: column;
  justify-content: space-between;
  padding: 24px 30px;
  font-size: 24px;
  line-height: 140%;
  display: flex;
}

.hero__info--item-big .hero__info--title {
  margin-bottom: auto;
}

.hero__info .hero__info--title {
  margin: 0 0 4px;
  font-weight: 600;
}

.desktop {
  text-align: center;
  background-color: #f5f5f7;
  margin: 0;
  padding: 20px 0 100px;
}

.desktop__video {
  border-radius: 8px;
  flex-direction: column;
  gap: 0;
  margin-top: 40px;
  display: flex;
  overflow: hidden;
}

.assumptions {
  padding: 80px 0;
}

